

.item{

    width: 250px;
    margin-bottom: 20px;
    /* border: 1px solid black; */
}

.item img{

    height: 200px;
    /* width: 200px; */
   
}

.item p{

    margin:6px 0px;
    margin-right: 100px;
}

.items-prices{

    display: flex;
    gap: 20px;

}

.item-prices-new{

    color: #374151;
    font-size: 18px;
    font-weight: 600;
}

.item-prices-old{
    color: #8c8c8c;
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
}

.item:hover{

    transform: scale(1.2);
    transition: 0.6s;
}